export default function MiniBarChart({ data = {} }) {
  const entries = Object.entries(data).sort((a,b) => a[0] < b[0] ? 1 : -1).slice(0,14).reverse();
  const max = Math.max(...entries.map(e => e[1] || 0), 1);
  return (
    <div className="flex gap-2 items-end h-28">
      {entries.map(([day, mins]) => (
        <div key={day} className="flex-1 flex flex-col items-center">
          <div className="w-full" style={{ height: `${(mins/max)*100}%` }}>
            <div className="rounded-t-md h-full bg-indigo-500" />
          </div>
          <div className="text-[10px] text-gray-500 mt-1">{new Date(day).toLocaleDateString(undefined, {month:'short', day:'numeric'})}</div>
        </div>
      ))}
    </div>
  );
}
