import { useState } from 'react';
import { API_BASE } from './api';

export default function TaskForm({ userId = 1, onCreated }) {
  const [title, setTitle] = useState('');
  const [duration, setDuration] = useState(60);
  const [deadline, setDeadline] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  function toISO(dtLocal) {
    if (!dtLocal) return null;
    const d = new Date(dtLocal);
    return isNaN(d.getTime()) ? null : d.toISOString();
  }

  async function submit(e) {
    e?.preventDefault();
    setError(null);
    if (!title.trim()) { setError('Enter a title'); return; }
    if (!duration || duration <= 0) { setError('Duration must be > 0'); return; }

    const payload = {
      user_id: userId,
      title: title.trim(),
      duration_minutes: Number(duration),
      deadline: toISO(deadline) || new Date(Date.now() + 24*3600*1000).toISOString(),
      priority: 2,
      category: 'study'
    };

    setLoading(true);
    try {
      const res = await fetch(`${API_BASE}/api/tasks`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload)
      });
      if (!res.ok) {
        const txt = await res.text();
        throw new Error(txt || `HTTP ${res.status}`);
      }
      const created = await res.json();
      setTitle(''); setDuration(60); setDeadline('');
      onCreated && onCreated(created);
    } catch (err) {
      console.error('Task create error', err);
      setError(err.message || 'Failed to create task');
    } finally { setLoading(false); }
  }

  return (
    <form onSubmit={submit} className="space-y-3">
      <label className="block">
        <span className="text-sm text-gray-600">Task</span>
        <input value={title} onChange={e=>setTitle(e.target.value)}
          className="mt-1 block w-full rounded-md border-gray-200 shadow-sm p-2"
          placeholder="e.g. Finish math practice" />
      </label>

      <div className="flex gap-2">
        <label className="flex-1">
          <span className="text-sm text-gray-600">Duration (min)</span>
          <input type="number" value={duration} onChange={e=>setDuration(e.target.value)}
            className="mt-1 block w-full rounded-md border-gray-200 shadow-sm p-2" />
        </label>

        <label className="flex-1">
          <span className="text-sm text-gray-600">Deadline</span>
          <input type="datetime-local" value={deadline} onChange={e=>setDeadline(e.target.value)}
            className="mt-1 block w-full rounded-md border-gray-200 shadow-sm p-2" />
        </label>
      </div>

      {error && <div className="text-sm text-red-600">{error}</div>}

      <div className="flex justify-end">
        <button type="submit" disabled={loading}
          className={`px-4 py-2 rounded-md text-white ${loading ? 'bg-gray-400' : 'bg-indigo-600 hover:bg-indigo-700'}`}>
          {loading ? 'Adding...' : 'Add Task'}
        </button>
      </div>
    </form>
  );
}
