import { useEffect, useState } from 'react';
import { API_BASE } from './api';

export default function TasksList({ userId = 1, refreshKey = 0 }) {
  const [tasks, setTasks] = useState([]);
  const [loading, setLoading] = useState(true);
  const [err, setErr] = useState(null);

  async function load() {
    setLoading(true); setErr(null);
    try {
      const res = await fetch(`${API_BASE}/api/tasks/user/${userId}`);
      if (!res.ok) throw new Error('Failed to fetch tasks');
      const json = await res.json();
      setTasks(json || []);
    } catch (e) { console.error(e); setErr(e.message || 'Error'); }
    finally { setLoading(false); }
  }

  useEffect(() => { load(); }, [userId, refreshKey]);

  async function markDone(id) {
    try {
      const res = await fetch(`${API_BASE}/api/tasks/${id}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ status: 'completed' })
      });
      if (!res.ok) throw new Error('Failed to update');
      await load();
    } catch (e) { console.error(e); alert('Could not mark done'); }
  }

  async function removeTask(id) {
    if (!confirm('Delete this task?')) return;
    try {
      const res = await fetch(`${API_BASE}/api/tasks/${id}`, { method: 'DELETE' });
      if (!res.ok) throw new Error('Failed to delete');
      await load();
    } catch (e) { console.error(e); alert('Could not delete'); }
  }

  if (loading) return <div>Loading tasks...</div>;
  if (err) return <div className="text-sm text-red-600">Error: {err}</div>;

  return (
    <div className="space-y-3">
      {tasks.length === 0 && <div className="text-sm text-gray-500">No tasks yet — add one to get started.</div>}
      {tasks.map(t => (
        <div key={t.id} className="bg-white shadow-sm rounded-lg p-3 flex items-center justify-between">
          <div>
            <div className="font-semibold">{t.title} {t.priority > 1 && <span className="ml-2 text-xs bg-indigo-100 text-indigo-700 px-2 py-0.5 rounded">High</span>}</div>
            <div className="text-xs text-gray-500">{t.category} • {t.duration_minutes} min</div>
            <div className="text-xs text-gray-400">Deadline: {t.deadline ? new Date(t.deadline).toLocaleString() : '-'}</div>
            {t.scheduled_start && <div className="text-xs text-indigo-600">Scheduled: {new Date(t.scheduled_start).toLocaleString()}</div>}
          </div>

          <div className="flex flex-col items-end gap-2">
            {t.status !== 'completed' ? (
              <button onClick={() => markDone(t.id)} className="text-sm bg-green-50 text-green-700 px-3 py-1 rounded">Mark done</button>
            ) : (
              <span className="text-sm text-gray-500">Completed</span>
            )}
            <div className="flex gap-2">
              <button onClick={() => removeTask(t.id)} className="text-sm text-red-600">Delete</button>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}
