// frontend/components/api.js
// Exports a reliable API_BASE that falls back to localhost:4000 in dev
export const API_BASE =
  (typeof window !== 'undefined' && process?.env?.NEXT_PUBLIC_API_BASE) ||
  process.env.NEXT_PUBLIC_API_BASE ||
  'http://localhost:4000';

// URL for the hero image served by your backend static endpoint
export const HERO_IMAGE_BACKEND = `${API_BASE}/static/hero.jpg`;
